input ,textarea{
  margin-top: 25px;
  border: none !important;
  box-shadow: 5px 5px 0px #cacbce, -5px -5px 0px #ffffff !important;
  background-color: #ebecf0 !important;
  background: #ebecf0;
  border-radius: 20px !important;

}
.form-control{
  box-shadow: none !important;

}
.menu-icon {
  font-size: 70px;
  color: orange;
  margin: 20px;
  cursor: pointer;
  z-index: 1000;
}

.showHeader {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: fixed;
  top: 0;
  right: 0;
  width: 50%;
  height: 65vh;
  background-color: rgb(29, 38, 80);
  border-bottom-left-radius: 50%;
  opacity: 0.85;
  transition: all 0.5s;
  z-index: 99;
}

ul li {
  list-style: none;
  color: white;
  margin-top: 25px;
}

ul li a {
  color: white;
  text-decoration: none;
}

.hideHeader {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: fixed;
  top: 0;
  right: 0;
  overflow: hidden;
  height: 0;
  width: 0;
  transition: all 0.5s;
}

.active{
  border: 3px solid cyan;
padding: 5px;
border-radius: 10px;
}

.active a{
font-weight: bold;
color: cyan;
}

@media(max-width:750px){

.showHeader {
  color: white;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: fixed;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
  background-color: rgb(29, 38, 80);
  border-bottom-left-radius: 50%;
  border-top-left-radius: 50%;
  opacity: 0.85;
  transition: width 0.5s;
  overflow: hidden;
  border-left: 5px solid white;
}
.menu-icon {
  font-size: 45px;
  color: orange;
  cursor: pointer;
  z-index: 1000;
}
}

.contact img{
  height: 50vh;
  width: 100%;
}

.footer{
  overflow: hidden;
}

.footer-content{
  background-color: #001220;
  text-align: center;
  color: white;
}

.footer-icons svg{
  color: white;
}
.footer-icons:hover{
  color: cyan;
  transform: scale(1.5);
  cursor: pointer;
}