.intro{
    height: 100vh;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.dev-stack{
    color: black;
    transition: all 0.5s;
}
.dev-stack:hover{
    color: white;
    background-color: #001220;
    transform: scale(1.05);
    box-shadow: none;
}

