.project {
  padding: 20px;
  border: 1px solid rgba(0, 0, 0, 0.486);
  border-radius: 10px;
  margin-top: 50px;
  animation: project-anim 10s infinite linear;
}
.project-list {
  overflow: hidden;
}
.project img {
  height: 220px;
  width: 100%;
}

.project:hover > .project-content {
  opacity: 1;
  transform: scale(1.05);
  cursor: pointer;
}

.project:hover {
  animation-play-state: paused;
}
.project-content {
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 20px;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.8);
  color: white;
  font-weight: 500;
  border-radius: 10px;
  transition: all 0.5s;
  opacity: 0;
}

@keyframes project-anim {
  0% {
    transform: rotate(-5deg);
  }

  25% {
    transform: rotate(0deg);
  }

  50% {
    transform: rotate(5deg);
  }

  75% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(-5deg);
  }
}

.course-intro {
  height: 100vh;
  width: 100%;
  background-size: cover;
  background-position: cover;
  display: flex;
  align-items: center;
  justify-content: center;
}

.course-intro-content {
  width: 50%;
  color: white;
  border-left: 5px solid cyan;
  padding-left: 30px;
  animation: course-intro-anim 10s infinite linear;
}

.course-intro-content div {
  border-right: 2px solid white;
}

.course-intro-content h1 {
  font-size: 75px;
  border-bottom: 2px solid white;
  -webkit-text-fill-color: transparent;
  -webkit-text-stroke-color: cyan;
  -webkit-text-stroke-width: 2px;
}



@media(max-width:750px){
    .course-intro-content {
        width: 100%;
        color: white;
        border-left: 5px solid cyan;
        padding: 20px;
        margin: 20px;
      }
      
.course-intro-content h1 {
    font-size: 35px;
    border-bottom: 2px solid white;
    -webkit-text-stroke-width: 1px;
  }

  
}



@keyframes course-intro-anim {
    0% {
      transform: rotate(-5deg);
    }
  
    25% {
      transform: rotate(0deg);
    }
  
    50% {
      transform: rotate(5deg);
    }
  
    75% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(-5deg);
    }
  }
